body{
  background-color: #ccc;
}

img{
  width:100%;
  height:350px;
  object-fit: cover;
}

.main-bbs{
  margin-top:10px;
}

.main-bbs:nth-child(2){
  float:right;
}

.notice{
  position:relative;
  display:inline-block;
  min-width:fit-content;
  width:49%;
  height:200px;
  border:1px solid #fff;
}

.notice h4{
  margin:0;
  margin-top:10px;
  margin-left:20px;
}

.m-post-list{
  margin-left:15px;
}

.m-post-list span{
  margin-right:10px;
}

.notice p{
  margin:0;
}

.note{
  display:inline-block;
  width:50%;
}

.comment{
  width:100%;
}
.more{
  position: absolute;
  bottom:10px;
  right:10px;
}