html,
body {
  height: 100%;
}

.app {
  min-height: 100%;
  position: relative;
  padding-bottom: 60px;
}

.black-nav {
  display: flex;
  background-color: black;
  width: 100%;
  color: white;
}

.img-link {
  text-align: center;
}

.img-link img {
  width: 90px;
  height: 100px;
  object-fit: fill;
  margin-top: 15px;
  margin-right: 50px;
}

#title {
  font-size: 1.3em;
  cursor: default;
  margin-left: 10px;
}

.black-nav li {
  cursor: default;
}

.top-menu {
  display: flex;
  list-style: none;
}

.top-menu li {
  position: relative;
  margin-top: 15px;
  margin-right: 50px;
  list-style: none;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #111;
  min-width: 160px;
  padding: 12px;
  left: -13px;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown li:hover {
  color: skyblue;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid grey;
  cursor: pointer;
}

.modal {
  padding-bottom: 20px;
}

span {
  float: right;
  margin-right: 20px;
}

footer {
  position: relative;
  bottom: 0;
}

a {
  text-decoration: none;
  color: dodgerblue;
  font-weight: bold;
}

footer {
  background-color: #111;
  color: white;
  position: absolute;
  width: 100%;
  bottom: 0;
}

footer p {
  text-align: center;
}

.work_text{
  margin-top:50px;
  text-align:center;
}

.work_img img{
  object-fit: fill;
  height:600px;
}